
import RenderState from './RenderState';

import * as fpe from '@floorplanner/fp.engine.new';

const FP_API_TOKEN="eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhY3Rpb24iOiJsb2dpbiIsIm1vZGVsIjoiVXNlciIsImlkIjoxODE2NjY0MiwiaWF0IjoxNTE4MDA0ODc1fQ.zniSwCSbwqafIkqTzo-vyUL48h8W4HosaGOPD9bHYgY";
const FP_URL = 'https://site.floorplanner.com/api/v2/';

export default class RenderService {

    constructor() {
        this.state = new RenderState();

        this.state.status = 'Initializing application...';
    }


    async load(project_id=56732862) {

        this.state.status = `loading project ${project_id}`;

        const url = `${FP_URL}projects/${project_id}.fml`;

        fpe.io.setAuthToken(FP_API_TOKEN);

        let project = await fpe.io.getProjectJson(url.replace(/\.fml/, '/fml'), true);
        let design = project.floors[0].designs[0];

        Object.keys(project.settings).forEach(k => {
            design.settings[k] = project.settings[k]
        })

        design.settings.showGrid = true;
        design.settings.showDropShadows = true;

        design.floorplan = new fpe.Floorplan(design.settings);

        design = await fpe.io.setAssets(design);

        await design.floorplan.importFloorplan(design, design.drawing);

        this.state.status = `project ${project_id} loaded`;

        this.state.design = design;


        console.log(design.settings)
        return design;
    }
};
