import React from 'react';
import PropTypes from 'prop-types';
import { observer, inject } from 'mobx-react';

const Footer = ({ rs_state: { version, status } }) =>
    <div id="footer">
        <div id="build_number">
            {version} {status}
        </div>
        <div id="contact">
            <a shape="rect"
                target="_blank"
                rel="noopener noreferrer"
                href="http://www.floorplanner.com">
                &copy; FLOORPLANNER.COM
            </a>
            |
            <a shape="rect" href="mailto:info@floorplanner.com">
                info@floorplanner.com
            </a>
        </div>
    </div>;

Footer.propTypes = {
    rs_state: PropTypes.object
};

export default inject('rs_state')(observer(Footer));
