import React from 'react';
import { autorun, when } from 'mobx';
import { observer } from 'mobx-react';
import { inject } from 'mobx-react';
import PropTypes from 'prop-types';

import * as fp from '@floorplanner/fp.engine.new';

class Render extends React.Component {
    constructor(props) {
        super(props);

        this.canvas_ref = React.createRef();

        this.renderer = null;

        autorun(() => console.log(this.props.rs_state.status));
    }

    componentDidMount() {
        const { rs_state, RS } = this.props;
        const { current } = this.canvas_ref;

        autorun(() => {
            try {
                this.renderer = new fp.PixiRenderer(current.parentElement, {}, false)
            } catch (e) {
                console.log(e.message)
            }
        });

        autorun(async () => {

            if (rs_state.design) {
                // a new design is loaded, render it
                let design = rs_state.design;

                const bounds = fp.getBbox(design.floorplan.state)

                design.settings.showGrid = rs_state.showGrid;

                this.renderer.clear();
                //this.renderer.renderer.clear()
                this.renderer.updateConfig(design.settings);
                this.renderer.resize(window.innerWidth, window.innerHeight);
                this.renderer.zoomAll(bounds);

                await this.renderer.update(design.floorplan.diff());

                this.renderer.render(this.renderer.stage)

                rs_state.status = `rendered design ${design.id}`;
                setTimeout(() => {
                    console.log(this.renderer.app.view)

                    document.dispatchEvent(new CustomEvent('design-ready', {detail: {design: design.id, state: rs_state.design}}))
                }, 200);
            }
        })
    }

    render() {
        return (
            <div ref={this.canvas_ref} />
        )
    }

}

Render.propTypes = {
    rs_state: PropTypes.object,
    RS: PropTypes.object
};

export default inject('rs_state')(observer(Render));
