import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'mobx-react';
import { autorun, when } from 'mobx';
import './styles/floorplanner.css';
import Render from './components/Render';
import Footer from './components/Footer';
import RenderService from './services/RenderService';

const RS = new RenderService();

document.addEventListener('design-ready', (e) => {
    console.log(e.detail)
})

window.loadDesign = () => {
    RS.load()
}

const Renderer = () => {
    return (
        <Provider rs_state={RS.state}>
            <Render RS={RS} />
            <Footer />
        </Provider>
    );
};

const loader = new PIXI.Loader()

loader.add('Arial', '/resources/arial.fnt').load(() => {
    render(<Renderer />, document.getElementById('root'));
    window.loadDesign()
});
