function createProjectXml(state, projectId, floorId) {
  var xmlString = "<project></project>";
  var parser = new DOMParser();
  xmlDoc = parser.parseFromString(xmlString, "text/xml");

  var project = xmlDoc.getElementsByTagName("project")[0];

  projectSettings(state, project);

  var floors = create("floors").attribute("type", "array").appendTo(project);

  // TODO: support multiple floors
  var floor = create("floor").appendTo(floors);

  floorSettings(state, floor);

  var designs = create("designs").attribute("type", "array").appendTo(floor);

  // TODO: support multiple designs
  var design = create("design").appendTo(designs);

  designSettings(state, design);

  makeLines(state, design);

  makeAreas(state, design);

  makeObjects(state, design);

  return xmlDoc;
}

function createDesignXml(state, config) {
  var xmlString = "<design version='2.0'></design>";
  var parser = new DOMParser();
  xmlDoc = parser.parseFromString(xmlString, "text/xml");

  var design = xmlDoc.getElementsByTagName("design")[0];
  designSettings(state, design, config);
  makeLines(state, design);
  makeAreas(state, design);
  makeObjects(state, design);
  return xmlDoc;
}