function ensureCameras(project, maySave) {
  return Promise.all(project.floors.filter(function (f) {
    return f.cameras.length < 2;
  }).map(function (f) {
    return ensureCamera(project.id, f, maySave);
  })).then(function () {
    return project;
  });
}

function ensureCamera(pid, floor, maySave) {
  // this will be a bugfest!
  // orbital will be the first camera, walkthrough the second
  var safeCreate = function safeCreate(pid, fid, cam) {
    return maySave ? createCamera(pid, fid, cam) : Promise.resolve(cam);
  },
      safeUpdate = function safeUpdate(pid, fid, cam) {
    return maySave ? updateCamera(pid, fid, cam) : Promise.resolve(cam);
  };

  if (floor.cameras.length == 0) {
    // zero cameras are in plan, just make the 2 differnt kinds.
    return safeCreate(pid, floor.id, DEFAULT_ORBITAL_CAMERA).then(function (c) {
      floor.cameras.push(c);
      return floor;
    }).then(function () {
      return safeCreate(pid, floor.id, DEFAULT_WALKTHROUGH_CAMERA).then(function (c) {
        floor.cameras.push(c);
        return floor;
      });
    });
  } else {
    // one camera is present
    // let assume this is a walkthrough camera, so probably we add a orbital
    // better yet, lets check the type of camera,
    var kindToAdd = DEFAULT_ORBITAL_CAMERA;
    var update = false;
    if (!floor.cameras[0].type_name) {
      floor.cameras[0].type_name = "walkthrough";
      if (floor.cameras[0].name == "default") {
        floor.cameras[0].name = "Camera 2";
      }
      update = true;
    } else if (floor.cameras[0].type_name == "orbital") {
      kindToAdd = DEFAULT_WALKTHROUGH_CAMERA;
      console.assert(floor.cameras[0].type_name == "orbital");
    }

    var returnFunction = update ? safeUpdate(pid, floor.id, floor.cameras[0]) : Promise.resolve();
    return returnFunction.then(function () {
      return safeCreate(pid, floor.id, kindToAdd).then(function (c) {
        if (kindToAdd == DEFAULT_ORBITAL_CAMERA) {
          floor.cameras.unshift(c);
        } else {
          floor.cameras.push(c);
        }
        return floor;
      });
    });
  }
}

